import React from 'react'
import '../container/styles/style.css'

import eyeHiddenLogo from '../container/assets/eye_hidden.png'
import turkeyLang from '../container/assets/tr.png'
import englishLang from '../container/assets/en.png'
import deutchLang from '../container/assets/de.png'
import netherlandLang from '../container/assets/be.png'
import logo from '../container/resources/3,4ab857badb3d.webp'



export default function Header() {
    return (
        <div>
            
        <header>
        <div className="actions">
            <a className="logo" href="https://45bet.com/en/">
                <img src={logo} alt="logo" />
            </a>
            <div className="material-icons menu">menu</div>
            <div className="go-back">
                <span className="material-icons">arrow_back</span>
                <span text_key="MENU__GO_BACK">Back</span>
            </div>
        </div>

        <div className="middle">
            <div className="nav-wrapper">
                <a className="logo" href="https://45bet.com/en/">
                    <img src="./resources/3,4ab857badb3d.webp" alt="logo" />
                </a>
                <div className="color-switcher">
                    <div className="chbx"></div>
                </div>
                <nav>
                    <ul className="nav">
                        <li><a href="https://45bet.com/en/sportsbook/prematch"><span className="icon sport"></span> <span text_key="MENU__SPORT">Sport Betting</span></a></li>
                        <li><a href="https://45bet.com/en/sportsbook/live"><span className="icon live"></span> <span text_key="MENU__LIVE">Live Betting</span></a></li>
                        <li><a href="https://45bet.com/en/casino/xlive"><span className="icon live"></span> <span text_key="MENU__XLIVE">Xlive</span></a></li>
                        <li><a href="https://45bet.com/en/casino/casino" className="active"><span className="icon casino"></span> <span text_key="MENU__CASINO">Casino</span></a></li>
                        <li><a href="https://45bet.com/en/casino/livecasino"><span className="icon livecasino"></span> <span text_key="MENU__LIVE_CASINO">Live Casino</span></a></li>
                        <li><a href="https://45bet.com/en/sportsbook/virtual"><span className="icon virtual"></span> <span text_key="MENU__VIRTUAL">Virtual Sport</span></a></li>
                        <li><a href="https://45bet.com/en/sportsbook/esports"><span className="icon esports"></span> <span text_key="MENU__ESPORTS">Esport</span></a></li>
                    </ul>
                </nav>
            </div>

            <div className="auth-bar">
                <div className="authorize">
                    <form className="auth">
                        <ul>
                            <li>
                                <span className="forgot-pass" text_key="LOGIN__FORGOT_PASSWORD?">Forgot Password ?</span>
                            </li>
                            <li><a className="btn green register" href="https://45bet.com/en/static/registration" text_key="LOGIN__SIGNUP">Sign up</a></li>
                            <li>
                                <input type="text" name="userName" placeholder_key="LOGIN__USERNAME" placeholder="Username" />
                                <div className="material-icons icon">person</div>
                            </li>
                            <li>
                                <input type="password" name="password" placeholder_key="LOGIN__PASSWORD" placeholder="Password" />
                                <div className="material-icons icon">lock</div>
                            </li>
                            <li>
                                <button type="submit" className="btn gray login" text_key="LOGIN__LOG_IN">Log In</button>
                            </li>
                        </ul>
                    </form>
                </div>
                <div className="user-info">
                    <ul>
                        <li className="username"><span text_key="SIGNUP_AFTER_LOGIN__WELCOME">Welcome</span>
                            <a href="https://45bet.com/en/account/personalinfo"></a>
                        </li>
                        
                        <li className="balance">
                            <span text_key="SIGNUP_AFTER_LOGIN__BALANCE~: ">Balance : </span> 
                            <img src={eyeHiddenLogo} className="lock" alt="hidden" /> 
                            <span className="amount"></span> 
                            <span className="currency-str"></span>
                            <span className="material-icons refresh-balance">refresh</span>
                        </li>

                        <li>
                            <a className="btn green deposit" href="https://45bet.com/en/account/deposit" text_key="SIGNUP_AFTER_LOGIN__DEPOSIT">Deposit</a>
                        </li>
                        <li>
                            <a className="btn gray account-info light" href="https://45bet.com/en/account/personalinfo">
                                <span className="material-icons">person</span> 
                                <span text_key="SIGNUP_AFTER_LOGIN__MY_ACCOUNT">My account</span>
                            </a>
                        </li>
                        <li className="logout">
                            <span className="material-icons">power_settings_new</span>
                        </li>
                    </ul>
                </div>
                <div className="loader"></div>
            </div>

            <div className="change-lang">
                <div className="current-lang">
                    <img src={englishLang} lang="en" id="en-language" alt="en" />
                    <img src={turkeyLang} lang="tr" alt="tr" />
                    <img src={deutchLang} lang="de" alt="de" />
                    <img src={netherlandLang} lang="nl" alt="be" />
                    <span className="current-lang">en</span>
                    <span className="material-icons">keyboard_arrow_down</span>
                </div>
                <ul>
                    <li lang="en">
                        <a href="https://45bet.com/en">
                            <img src="./assets/en.png" alt="en" />
                            <span text_key="LANGUAGE__EN-US">EN - English</span>
                        </a>
                    </li>
                    <li lang="tr">
                        <a href="https://45bet.com/tr">
                            <img src="./assets/tr.png" alt="tr" />
                            <span text_key="LANGUAGE__TR-TR">TR - Türkçe</span>
                        </a>
                    </li>
                    <li lang="de">
                        <a href="https://45bet.com/de">
                            <img src="./assets/de.png" alt="de" />
                            <span text_key="LANGUAGE__DE-DE">DE - Deutsch</span>
                        </a>
                    </li>
                    <li lang="nl">
                        <a href="https://45bet.com/nl">
                            <img src="./assets/be.png" alt="be" />
                            <span text_key="LANGUAGE__NL-NL">NL - Nederlands</span>
                        </a>
                    </li>
                </ul>
            </div>

        </div>
    </header>

    <div className="header-helper"></div>
    <div className="overlay signin-popup">
        <form className="popup signin">
            <h2 text_key="LOGIN__LOG_IN">Log In</h2><span className="material-icons close">close</span>
            <ul>
                <li>
                    <input type="text" name="userName" placeholder_key="LOGIN__USERNAME" required="" placeholder="Username" />
                </li>
                <li>
                    <input type="password" name="password" placeholder_key="LOGIN__PASSWORD" required="" placeholder="Password" />
                </li>
                <li>
                    <button type="submit" className="btn" text_key="LOGIN__LOG_IN">Log In</button>
                </li>
            </ul>
            <p className="err-text main-err" text_key="LOGIN__ERROR">Error</p>
            <p className="success-text main" text_key="LOGIN__SUCCESS">Success</p>
        </form>
    </div>

    <div className="overlay forgot-password">
        <form className="popup send-mail">
            <h2 text_key="F_PASSWORD__FORGOT_PASSWORD">Forgot Password ?</h2><span className="material-icons close">close</span>
            <ul>
                <li>
                    <input type="email" name="email" placeholder_key="F_PASSWORD__EMAIL" required="" placeholder="Email"/>
                </li>
                <li>
                    <button type="submit" className="btn" text_key="F_PASSWORD__SEND">Send</button>
                    <div className="loader-wrapper">
                        <div className="loader"></div>
                    </div>
                </li>
            </ul>
            <p className="err-text main-err"></p>
            <p className="success-text main"></p>
        </form>
        <div className="popup success">
            <h2 text_key="F_PASSWORD__FORGOT_PASSWORD">Forgot Password ?</h2><span className="material-icons close diff-action">close</span>
            <p className="success-text"><span text_key="F_PASSWORD__MAIL_SENT">Mail Sent</span> <span className="material-icons">done</span></p>
            <div className="btn-wrapper">
                <div className="btn" text_key="F_PASSWORD__OK">Ok</div>
            </div>
        </div>
    </div>

    <div className="overlay new-message">
        <div className="popup"><span className="material-icons close">close</span>
            <div className="content">
                <div className="banner-wrapper"><img /></div>
                <div className="text"></div>
            </div>
        </div>
    </div>



        </div>
    )
}
